body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

.header {
  text-align: center;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header .logo__link {
  width: 40px;
  height: 40px;
}

.header .logo__image {
  width: 100%;
}

.header .nav__list {
  margin: 0;
  padding: 0;
  display: flex;
}

.header .nav__item {
  list-style-type: none;
}

.header .nav__item:not(:first-child) {
  margin-left: 20px;
}

.header .nav__item-link {
  text-transform: uppercase;
  color: #060b35;
  padding: 23px 0;
  text-decoration: none;
  transition-duration: .3s;
  display: block;
}

.header .nav__item-link:hover {
  color: #00acdc;
}

.header .nav__item-link.is-active {
  color: #00acdc;
  position: relative;
}

.header .nav__item-link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  transition-duration: .3s;
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: #060b35;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product {
  color: #616070;
  margin-top: 4px;
}

.card__stars {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin: 24px 0 16px;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-count {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  align-content: center;
  width: 100%;
  height: 40px;
  padding: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition-duration: .3s;
}

.card__button:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.container {
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width <= 1023px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width <= 767px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width <= 487px) {
  .container {
    grid-template-columns: repeat(1, 200px);
  }
}

